import React, { Component} from "react";
import ServTitle from '../Home/ServTitle';
import JobsTabItems from '../Jobs/JobsTabitems';
import { translate } from "react-translate";


const TabJobsEn = require('../../assets/translations/en.json');
const TabJobsEs = require('../../assets/translations/es.json');
let lang = null;

class JobsTab extends Component {

  render() {

    let {¬†t } = this.props;

    const ActualLang = localStorage.getItem("lenguaje") ? localStorage.getItem("lenguaje") : 'en';
   
    if (ActualLang === 'en') {
      lang = TabJobsEn
    } else {
      lang = TabJobsEs
    }
    
    return (
      <section className="jobs_area " >
        <div className="container">
          <div className="subTitle_job">
             <ServTitle ClassTitle="text-left" Title={t(this.props.title)} TitleP={t(this.props.titleD)} />
          </div>
          <div className="jobs_content">
            <div className="tab-content row jobs_tab_content">
              
              {
                lang.jobs_items.jobtabitem.map((item, i) => {

                  
                  return (
                    <div className="tab-pane col-lg-6 fade show active"  id={item.hrefArea} role="tabpanel" aria-labelledby={item.idArea} key={i}>
                      <JobsTabItems  
                        nameJob = {item.nameJob}
                        descripJobs={item.descripJobs}
                        requirJobs={item.requirJobs}
                        sueldo = {item.sueldo}
                        time = {item.time}
                        experience = {item.experience}
                        vacantes = {item.vacantes}
                      />  
                                     
                    </div>
                  )
                })
              
              }             
            </div>
          </div>
        </div>
      </section>
    );
  }
}
export default translate('jobs_items')(JobsTab);
