import React, {Component} from 'react';
import SeoTitle from '../Title/SeoTitle';
import { translate } from "react-translate";
import Plx from "react-plx";
import Reveal from 'react-reveal/Reveal';



class OurValues extends Component {
    render(){

        const ParallaxAboutStory = [
            {
              start: 'self',
              duration: 400,
              properties: [
                
                {
                    startValue: 0,
                    endValue: 1,
                    property: "opacity"
                },
                {
                    startValue: -50,
                    endValue: 0,
                    property: "translateX"
                  }
              ]
            }
        ];

        let {¬†t } = this.props;

        return(
            <section className="our_history_area " id='story'>
                <h2 className="text-center t_color mb-5 f_600 pb-5 f_size_40">{t(this.props.subtitle_value)}</h2>
                <div className="container history_colum">
                <img className={`img_fondo_content `} src={require ('../../img/about/Dreamcode_IlustracioÃÅnZigZag.png').default}  alt=""/>
                    <div className="valores">
                        {/* 1 */}
                        <div className="row align-items-center innovacion">
                            <div className="col-lg-5">
                                <Reveal effect="fadeInLeft">
                                    <Plx  className="our_history text-center" parallaxData={ParallaxAboutStory}>
                                        <SeoTitle 
                                            Title={t(this.props.value_innovation)} Tclass2="t_color"
                                            TitleP={t(this.props.innovation_descrip)}
                                            Tclass='text-left'/>
                                    </Plx>
                                </Reveal>
                            </div>
                            <div className="col-lg-6">
                                <div className="our_history_content">
                                    <img className="img-fluid fadeInUp" src={require("../../img/about/innovacion_tech.png").default} alt=""/>
                                </div>
                            </div>            
                        </div>
                         {/* 2 */}
                        <div className="row align-items-center performance">
                            <div className="col-lg-6">
                            <div className="our_history_content">
                                <img className="img-fluid fadeInUp" src={require("../../img/about/performance.png").default} alt=""/>
                            </div>
                            </div>
                            <div className="col-lg-5">
                                <Reveal effect="fadeInLeft">
                                    <Plx  className="our_history text-center" parallaxData={ParallaxAboutStory}>
                                    <SeoTitle 
                                        Title={t(this.props.value_performance)} 
                                        TitleP={t(this.props.performance_descrip)}
                                        Tclass='text-left'/>
                                    </Plx>
                                </Reveal>
                            </div>                        
                        </div>
                        {/* 3 */}
                        <div className="row align-items-center motivacion pt-4">
                            <div className="col-lg-5">
                                <Reveal effect="fadeInLeft">
                                    <Plx  className="our_history  text-center" parallaxData={ParallaxAboutStory}>
                                    <SeoTitle 
                                        Title={t(this.props.value_motivation)} 
                                        TitleP={t(this.props.motivation_descrip)}
                                        Tclass='text-left'/>
                                    </Plx>
                                </Reveal>
                            </div>
                            <div className="col-lg-7">
                                <div className="our_history_content">
                                    <img className="img-fluid fadeInUp" src={require("../../img/about/motivacion.png").default} alt=""/>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}


export default translate('values_about')(OurValues);