import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal/';
import { translate } from "react-translate";


class BlogBanner extends Component{
    render(){


            let {¬†t , idBanner, classBanner2} = this.props;

        return(
            <section className={`banner_area ${classBanner2} sec_pad2`} id={idBanner}>
                <div className="container">
                    <div className="">                   
                        <div className="row">
                            <div className="col-md-12 text-center seo_banner_content">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h1>{t(this.props.title_banner2)}</h1>        
                                </Reveal>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </section>
        )
    }    
}


export default translate('sec_banner')(BlogBanner);
