import React, { Component } from "react";
import { Helmet } from "react-helmet";
import { translate } from "react-translate";

class MetaDecorator extends Component {
    render() {
  
      let {t, url} = this.props;
  
      return (   
        <Helmet>
            <meta property="og:title" content= {t(this.props.meta_title)} />
            <meta property="og:url" content={`https://dreamcodesoft.com/${url}`} />
            <meta property="og:description" content= {t(this.props.meta_description)} />
            <title>{t(this.props.meta_title)}</title>
            <meta name="description" content={t(this.props.meta_description)} />
        </Helmet>
       );
    };
  };




  export default translate('metaDecorator')(MetaDecorator);