import React, {Component} from 'react';
import Teamitem from './Teamitem';
import Reveal from 'react-reveal';
import SeoTitle from '../Title/SeoTitle';
import { translate } from "react-translate";

class Team extends Component{
    render(){

        let {¬†t } = this.props;
       
        return(
            <section className="experts_team_area mt_80" id="team">
                <div className="container">
                    <div className="experts_team_content">
                        <div className="seo_title_area">
                            <SeoTitle 
                                Title={t(this.props.title_team)} 
                                Tclass="text-center" Tclass2="t_color"/>
                        </div>
                        <Reveal bottom cascade>
                        <div className="row">
                            <div className="col-lg-4 col-sm-12">
                                <Teamitem 
                                    teamImage="horacio.png" 
                                    memberN="Horacio Borrero" 
                                    memberd={t(this.props.horacio_name)}
                                    memberE={t(this.props.horacio_descrip)}/>
                            </div>
                            <div className="col-lg-4 col-sm-12">
                                <Teamitem 
                                    teamImage="francisco.png" 
                                    memberN="Francisco Chaves" 
                                    memberd={t(this.props.francisco_name)}
                                    memberE={t(this.props.francisco_descrip)}/>
                            </div>
                            <div className="col-lg-4 col-sm-12">
                                <Teamitem 
                                    teamImage="jonathan.png" 
                                    memberN="Jonathan Ortiz" 
                                    memberd={t(this.props.jonathan_name)}
                                    memberE={t(this.props.jonathan_descrip)}/>
                            </div>
                        </div>
                    </Reveal>
                    </div>
                </div>
            </section>
        
        )
    }    
}
export default translate('team_about')(Team);