import React, {Component} from 'react';
import Slider from 'react-slick';
import Markdown from "react-markdown"
import postlist from "../../posts.json"
import postlistEs from "../../postsES.json"

let lang = null;

class HomeSliderBlog extends Component {


   render(){

    let {¬†language} = this.props;
   
    if (language === 'en') {
      lang = postlist
    } else {
      lang = postlistEs
    }


        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 4000,
            slidesToShow: 1,
            slidesToScroll: 1,
        };


        lang.sort((a, b) => new Date(a.date).getTime() > new Date(b.date).getTime())

        const excerptList = lang.map(post => {
            return post.content.split(" ").slice(0, 30).join(" ") + "..."
        })

        return(
            <div>
                <section className="blog_home_area bg-light">
                                <Slider className="blog_slider" {...settings}>
                                { lang.map((post, i) => {
                                    return (
                                                <div className="blog_item text-center left" key={i}>
                                                    <div className="row">
                                                        <div className="col-lg-7 pr-0">
                                                            <img className="img-fluid w-100" src={require(`../../img/posts-Img/${post.imglink}`).default} alt=""/>
                                                        </div>
                                                        <div className="col-lg-5 p-0 contentBlog">
                                                            <div className="text-left contentBlogItem">
                                                                <div className="feedback">
                                                                    <a href={`/post/${post.url}`}>
                                                                        <h2 className="text-uppercase f_p f_size_20 f_600 w_color mb_20">{post.title}</h2>
                                                                    </a>
                                                                    <Markdown children={excerptList[i]} skipHtml={false} className="post-text"/>
                                                                    <a href={`/post/${post.url}`} className="w_color learn_btn_two">Read More</a>
                                                                </div>
                                                             </div>
                                                        </div>
                                                    </div>    
                                                </div>
                                            )
                                        })
                                    }
                                </Slider>
                </section>
            </div>    
        )
   }
}
export default HomeSliderBlog;