import React, {Component} from 'react';
import { translate } from "react-translate";

import emailjs from 'emailjs-com';
import ReCAPTCHA from "react-google-recaptcha";


class FormJobs extends Component {

    
    render(){
        let {¬†t } = this.props;
       
        function sendEmail(e) {
            e.preventDefault();
        
            emailjs.sendForm('service_gmail', 'template_dreamcode', e.target, 'user_DK3x36R2nsn4W5NwGBIou')
              .then((result) => {
                  console.log(result.text);
              }, (error) => {
                  console.log(error.text);
              });
              e.target.reset()
          }
        
          function onChange(value) {
            console.log("Captcha value:", value);
          }



        return(
            <section className="contact_info_area sec_pad bg_color" id="contact">
                <div className="container">
                    <div className="row">
                  
                        <div className="contact_form col-lg-12">
                            <h2 className="f_p f_size_22 t_color3 f_600 l_height28 mb_40">{t(this.props.title)}</h2>
                            <form onSubmit={sendEmail} className="contact_form_box" method="post" id="contactForm">
                                <div className="row">
                                    <div className="col-lg-6">
                                        <div className="form-group text_box">
                                            <input type="text" id="name" name="name" placeholder={t(this.props.name)} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group text_box">
                                            <input type="text" name="email" id="email" placeholder={t(this.props.email)} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <input type="text" id="subject" name="subject" placeholder={t(this.props.subject)} onChange={this.handleChange}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box">
                                            <textarea onChange={this.handleChange} name="message" id="message" cols="30" rows="10" placeholder={t(this.props.message)}></textarea>
                                        </div>
                                    </div>
                                    <div class="pl-3 form-group">
                                        <label for="exampleFormControlFile1">Agregue su Hoja de Vida</label>
                                        <input type="file" class="form-control-file" id="exampleFormControlFile1" />
                                    </div>
                                    
                                </div>
                                <div className="mb-3">
                                    <ReCAPTCHA
                                        sitekey="6LcBxgkcAAAAAIY4DVwpYgaoTlqjLB0w-5N_2x_2"
                                        onChange={onChange}
                                    />
                                </div>    
                                <button type="submit" className="mb-4 btn_six slider_btn">{t(this.props.btn)}</button>
                            </form>
                            {/* {emailStatus ? emailStatus : null} */}
                            <div id="success">Your message succesfully sent!</div>
                            <div id="error">Opps! There is something wrong. Please try again</div>
                        </div>
              
                    </div>
                    
                </div>
            </section>
        )
    }
}



export default translate('contact_form')(FormJobs);