import React, { Component} from "react";
import JobsBanner from '../components/Banner/JobsBanner';
import BlogBanner from '../components/Banner/BlogBanner';
import JobsTab from '../components/Jobs/JobsTab';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';
import ReactGA from 'react-ga';
import "../components/Jobs/jobs.css"
import Fondo from '../components/Fondo';

    // Configuraci√≥n de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/jobs");


class Jobs extends Component {



    render() {


        return (

        <div className="body_wrapper jobs menu_white">           
            <MetaDecorator 
                meta_title="metaTitle_Jobs"
                meta_description="metaDescription_Jobs"
                url="jobs"
            />
            <BlogBanner 
                title_banner2 = "title_banner_Jobs"
                idBanner="bannerBlog"
                classBanner2="banner_job "
            />
            <Fondo ClassFondoImg2="d-none" ClassFondoImg3="d-none"/>
            <JobsTab 
                title="title" 
                titleD="titleD"
                job_area="job_area"
                language = {this.props.language}
                
            />

        </div>
    );
}
}
export default Jobs;