import React, {Component} from 'react';
import SeoTitle from '../Title/SeoTitle';
import { translate } from "react-translate";
import OurClients from '../Home/OurClients';



class OurHistory extends Component{
    render(){

        let {¬†t } = this.props;
       
        return(
            <section className="our_values_area" id="ourValues">
                <div className="container">
                    <div className="our_values_content">                     
                            <div className="seo_title_area pt-5 pl-5 pr-5 text-left">
                                <div className=" col-lg-6 mt-2">
                                    <SeoTitle 
                                        Title={t(this.props.title_story)}
                                        TitleP={t(this.props.descrip_story)}
                                        Tclass="text-white text-left" Tclass2="t_color"/>
                                </div>

                            </div>
                            <div className="mapaColombia">                             
                                    <div className="col-lg-4 position-absolute  pt-5 pr-5 mt-5">
                                        <h2 className="text-right mt-5 w_color font-italic l_height">{t(this.props.oficinas_text)}</h2>
                                    </div>
                                    <img src={require("../../img/about/mapaColombia.png").default} alt=""/> 
                            </div>
                        {/* ==== */}
                        <div className="seo_title_area pb-5 mt-5 pl-5 pr-5 text-right">
                        <SeoTitle 
                            Title={t(this.props.talentHuman_title)}
                            TitleP={t(this.props.talentHuman_descrip)}
                            Tclass="text-white text-right"/>

                        </div>
                    </div>
                  {/* ============================== */}
                    <div className="clients_content  ">
                        <img class="humanIcon" src={require("../../img/about/humanIcon.png").default} alt=""/> 
                        <div className="seo_title_area pt-5 text-center">
                            <SeoTitle 
                                Title={t(this.props.ourClients_title)}
                                TitleP={t(this.props.ourClients_descrip)}
                                Tclass="text-white"/>
                        </div>
                        <div className="cuadroClients ">
                            <div className="borderBox borderBox1">
                                <div className="borderBox borderBox2">
                                    <div className="borderBox borderBox3">
                                        <div className="borderBox borderBox4">
                                            <div className="borderBox borderBox5">
                                                <div className="borderBox borderBox6">
                                                        <div className="cuadroClients_cont bg-white ">
                                                            <OurClients />
                                                        </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>                             
                        </div>
                        <br></br>
                        <div className="seo_title_area mt-3 pb-5 text-center">
                        <SeoTitle 
                            Title={t(this.props.construyendoStory_title)}
                            Tclass="text-white"
                            TitleP={t(this.props.construyendoStory_descrip)}/>
                        </div>
                        <div className="d-flex justify-content-center p-5">
                            <img className="logoWhite" src={require("../../img/logo-white.png").default} alt=""/>
                        </div>
                    </div>      
                </div>
            </section>
        )
    }
}

export default translate('story_about')(OurHistory);