import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal/';
import { translate } from "react-translate";
import "./bannerHome.css"


class BannerHome extends Component{
    render(){


            let {¬†t , idBanner} = this.props;

        return(
            <section className={`home_banner_area sec_pad`} id={idBanner}>
                <div className="container">                                     
                        <div className="row">
                            <div className="col-md-6 text-left home_banner_content">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h1>{t(this.props.title_banner)}</h1>
                                </Reveal>                               
                            </div>
                            <div className="col-md-7 ">
                            </div>
                        </div>                    
                </div>
            </section>
        )
    }    
}


export default translate('sec_banner')(BannerHome);
