import React, {Component} from 'react';
import { translate } from "react-translate";

import emailjs from 'emailjs-com';
import ReCAPTCHA from "react-google-recaptcha";

class FormLanding extends Component {
 

    render(){
        let {¬†t } = this.props;
       
        function sendEmail(e) {
            e.preventDefault();
        
            emailjs.sendForm('service_gmail', 'template_dreamcode', e.target, 'user_YcsuuRh1YwBuhIZyur5gR')
              .then((result) => {
                  console.log(result.text);
              }, (error) => {
                  console.log(error.text);
              });
              e.target.reset()
          }
        
          function onChange(value) {
            console.log("Captcha value:", value);
          }



        return(
            <section className="mt-5 bg-light form_landing" id="landing">
                <div className="container">
                    <div className="row">

                        <div className="contact_form_landing col-lg-12 ">
                            <h2 className="f_p f_size_22 p_color f_600 l_height28 mb_40">{t(this.props.title)}</h2>
                            <form onSubmit={sendEmail} className="contact_form_box" method="post" id="contactForm">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input class="form-control" type="text" id="name" name="name" placeholder={t(this.props.name)} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input class="form-control" type="text" name="email" id="email" placeholder={t(this.props.email)} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input class="form-control" type="text" id="company" name="company" placeholder={t(this.props.company)} onChange={this.handleChange}/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box text-left">
                                        <label className="text-left" for="exampleFormControlSelect1">{t(this.props.titleServiceOpc)}</label>
                                            <select class="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref">

                                                <option value="1">{t(this.props.ServOpc1)}</option>
                                                <option value="2">{t(this.props.ServOpc2)}</option>
                                                <option value="3">{t(this.props.ServOpc3)}</option>
                                                <option value="4">{t(this.props.ServOpc4)}</option>
                                                <option value="5">{t(this.props.ServOpc5)}</option>
                                            </select>                                        
                                        </div>
                                    </div>
                                    
                                    <div className="col-lg-12">
                                        <div className="form-group ">
                                            <textarea class="form-control"  onChange={this.handleChange} name="message" id="message" cols="30" rows="10" placeholder={t(this.props.message)}></textarea>
                                        </div>
                                    </div>
                                </div>
                                <div className="mb-3">
                                    <ReCAPTCHA
                                        sitekey="6LcBxgkcAAAAAIY4DVwpYgaoTlqjLB0w-5N_2x_2"
                                        onChange={onChange}
                                    />
                                </div>    
                                <button type="submit" className="mb-1 btn_six slider_btn">{t(this.props.btn)}</button>
                            </form>
                            {/* {emailStatus ? emailStatus : null} */}
                            <div id="success">Your message succesfully sent!</div>
                            <div id="error">Opps! There is something wrong. Please try again</div>
                        </div>

                    </div>
                    
                </div>
            </section>
        )
    }
}

 
export default translate('landing_form')(FormLanding);