import React, {Component} from 'react';
import Isotope from 'isotope-layout/js/isotope';
import ImagesLoaded from 'imagesloaded/imagesloaded';
import Markdown from "react-markdown"
import { Link }  from "react-router-dom"
import postlist from "../../posts.json"
import postlistEs from "../../postsES.json"

let lang = null;

class PostList extends Component {

    constructor(){
        super();
        this.state= {
            activeItem: '*',
            language: 'en'
        }
    }

    componentDidMount() {
        let imgLoad = new ImagesLoaded('#articulos-post');
    
        imgLoad.on('progress', function(instance, image) {
            this.iso = new Isotope('#articulos-post', {
                itemSelector: '.portfolio_item',
                layoutMode: "masonry"
            });
        });        
    }
    
    onFilterChange = (newFilter) => {
        
        this.setState({activeItem: newFilter});
        if (this.iso === undefined) {
            this.iso = new Isotope('#articulos-post', {
            itemSelector: '.portfolio_item',
            layoutMode: "masonry"
            });
        }
    
    // this.iso.arrange({ filter: newFilter });   
      if(newFilter === '*') {
        this.iso.arrange({ filter: `*` });
      } else {
        this.iso.arrange({ filter: `.${newFilter}` });
      }
    }

    onActive = v => v === this.state.activeItem ? 'active' : '';

    render(){

        let {¬†language} = this.props;
   
        if (language === 'en') {
          lang = postlist
        } else {
          lang = postlistEs
        }



        //Ordena la lista por la fecha mas reciente 
        lang.sort((a, b) => new Date(a.date).getTime() > new Date(b.date).getTime())

        //Imprime solo las primeras 20 palabras del articulo
        const excerptList = lang.map(post => {
            return post.content.split(" ").slice(0, 20).join(" ") + "..."
        })


        return (
            <section className="postlist portfolio_area row">
                <div className="container">
                    <div id="portfolio_filter" className="portfolio_filter f_p mb_50">
                        <div data-filter="*" className={`All work_portfolio_item ${this.onActive('*')}`} onClick={() => {this.onFilterChange("*")}}>All</div>
                        <div data-filter="transformation" className={`digital work_portfolio_item ${this.onActive('transformation')}`} onClick={() => {this.onFilterChange("transformation")}}>Digital transformation </div>
                        <div data-filter="innovation" className={`innovacion work_portfolio_item ${this.onActive('innovation')}`} onClick={() => {this.onFilterChange("innovation")}}>Technology and Innovation</div>
                        <div data-filter="up-to-date" className={`stay work_portfolio_item ${this.onActive('up-to-date')}`} onClick={() => {this.onFilterChange("up-to-date")}}>Stay up-to-date</div>
                    </div>

                    <div className="row portfolio_gallery mb_30" id="articulos-post">
                        { lang.map((post, i) => {

                                return (                                      
                                    <div key={i}>
                                        <div className={`portfolio_item mb-30 col-lg-6 col-sm-12 ${post.category} `}>                                                               
                                                <div className="imgprueba">
                                                    <img className="img-fluid" src={require(`../../img/posts-Img/${post.imglink}`).default} alt=""/>
                                                </div>
                                                <div className="blog_content">
                                                    <h2 className="f_p f_size_30 f_600 t_color ">
                                                        <Link className="links f_p" to={`/post/${post.url}`}>{post.title}</Link>
                                                    </h2>
                                                    <div className="entry_post_info mb-2">
                                                        <a href="#">{post.date}</a>
                                                        <a href="#">{post.read}</a>                                                        
                                                        <a href="#">{post.category}</a>
                                                    </div>
                                                    <Markdown children={excerptList[i]} skipHtml={false} />
                                                    <div className="post_tag d-flex">
                                                        <p>{post.category}</p>
                                                    </div>                                                
                                                </div>
                                        </div>        
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </section>
        )
    }    
}
export default PostList;