import React, {Component} from 'react';
import Slider from 'react-slick';
import Fade from 'react-reveal/Fade';
import HomeServicesitems from './HomeServicesitems';
import "./homeServices.css"

const HomeservicesEn = require('../../assets/translations/en.json');
const HomeservicesEs = require('../../assets/translations/es.json');
let homeservices = null;

class HomeServices extends Component {
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
      next() {
        this.slider.slickNext();
      }
      previous() {
        this.slider.slickPrev();
      }
    render(){
        const settings = {
            dots: false,
            infinite: true,
            speed: 500,
            arrows: true,
            autoplay: true,
            autoplaySpeed: 400000,
            slidesToShow: 1,
            slidesToScroll: 1,
        };

        let {¬†language} = this.props;

        if (language === 'en') {
            homeservices = HomeservicesEn
          } else {
            homeservices = HomeservicesEs
          }


        return(
            <>
            <section className="testimonial_area sec_pad bg-light">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 d-flex align-items-center">
                            <div className="testimonial_title">
                                <Fade bottom cascade>
                                    <div className=" fadeInUp" data--delay="0.3s">
                                        <h2>{homeservices.service_home.serv_title}</h2>
                                        <p>{homeservices.service_home.serv_subtitle}</p>
                                        <a className="t_color mt-3 f_500 f_size_20 f_p" href="/services"><strong>Read More</strong></a>                                    </div>
                                </Fade>
                            </div>
                        </div>
                        <div className="col-lg-6 offset-lg-1 home-servicess">
                            
                            <div className="stratup_testimonial_info d-flex align-items-center">
                                <Slider ref={c => (this.slider = c)} className="services_slider_four" {...settings}>
                                    {
                                        homeservices.service_home.servitems.map((item, i) => { 
                                            return(
                                                <div className="item" key={i}>
                                                   <HomeServicesitems 
                                                        NameServ = {item.servTitle}
                                                        descriServ = {item.servDescrip}
                                                   />                                                 
                                                </div>
                                            )
                                        })
                                    }
                                </Slider>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            </>
        )
    }
}
export default HomeServices;