import React, { Component } from "react";
import JobsModal from "../Jobs/JobsModal.js";


class JobsTabItems extends Component {
  render() {

    let {¬†nameJob, descripJobs , requirJobs, time, experience, vacantes  } = this.props;
    

    let descripJobsHtml = descripJobs.replace(/\n/g,"<br ><br/>");


    return (
                <div className="media flex-wrap">
                  <div className="media-left">
                    <p><i className="icon_clock_alt"></i>{time}</p>
                    <p><i className="ti-write"></i>{experience}</p>
                    <p><i className="ti-location-pin"></i>{vacantes}</p>
                  </div>
                  <div className="media-body">
                    <h2 >{nameJob}</h2>
                    <p className="f_size_18">{descripJobsHtml}</p>     
                    <p className="f_size_15 p_color"><i>{requirJobs}</i></p>
                    <a href="mailto:hiring@dreamcodesoft.com?Subject=Solicitud%20para%20el%20puesto%20de%20" className="learn_btn">Apply Now</a>
                 
                    {/* < JobsModal /> */}
                  </div>
                </div>              
    );
  }
}
export default JobsTabItems;
