import React, {Component} from 'react';
import { translate } from "react-translate";
import { Link} from 'react-router-dom';
import ServTitle from './ServTitle';
import "./ourProcess.css"

class OurProcess extends Component {
    render(){
        let {¬†t } = this.props;
        
        return(
        
            <section className="our_process_area sec_pad" id="process">
                <div className="container processImg">
                    <ServTitle ClassTitle="text-center" Title={t(this.props.process_title)} TitleP={t(this.props.process_subtitle)} />
                    <div className="row mt-5">
                        <div className="col-lg-6 h-75 col_scrum" >
                            <div className="our_process_feedback"> 
                                <a className="w-100" href="/scrum">
                                    <div className="clients_feedback_item">
                                            <div className="feedback_body">
                                            <h3 className="f_name text-left">Agile Scrum</h3>
                                                <p className="text-left">{t(this.props.process_scrum_d)}</p>
                                                <Link to="/scrum" exact className="learn_btn mt-5 "><strong>{t(this.props.read_more)}</strong></Link>
                                            </div>
                                    </div>
                                </a>                            
                            </div>
                        </div>
                        <div className="col-lg-6 h-75 col_devops" >
                            <div className="our_process_feedback ">
                                <a className="w-100" href="/devops">
                                    <div className="clients_feedback_item">
                                        <div className="feedback_body">
                                            <h3 className="f_name text-right">DevOps</h3>
                                            <p className="text-right">{t(this.props.process_devops_d)}</p>
                                            <Link to="/devops" exact className="learn_btn mt-5 "><strong>{t(this.props.read_more)}</strong></Link>
                                        </div>               
                                    </div>                                  
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        
        )
    }
}

export default translate('process_home')(OurProcess);

