import './App.css';
import "../src/components/Landing/landing.css"
import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { TranslatorProvider } from "react-translate";

/* ---- Pages ----*/
import Home from "./Pages/Home";
import About from "./Pages/About"
import Devops from "./Pages/Devops";
import Jobs from "./Pages/Jobs";
import Scrum from "./Pages/Scrum";
import Contact from "./Pages/Contact";
import Blog from "./Pages/Blog"
import Services from "./Pages/Services"
import NotFound from "./Pages/404";

/* ---- Menu y Footer ----*/
//import Menu from './components/Header/Menu';
//import Footer from "./components/Footer/Footer";
import PostSingle from "./components/Blog/PostSingle"
import PagePost from './Pages/PagePost';


import Landing from "./Pages/Landing"
import Layout from './components/Layout';


class App extends Component{
 	
    constructor(props) {
        super(props);
        this.state = {
            language: 'en'
        }; 
        this.languageChange = this.languageChange.bind(this);  
    }

    
      
    languageChange(event) {
        this.setState({language: event.target.value});
        localStorage.setItem("lenguaje", event.target.value);
    }


    getLayout(lang){

    const translationsLang = require('./assets/translations/'+lang+'.json');
    
    
	   return  (    
        <TranslatorProvider translations={translationsLang}>	

                <Router> 
                    <Switch>
                        <Route exact path="/landing" component={() => <Landing value={this.state.value} language={this.state.language} onChangeLanguage={this.languageChange} />} />  
                        <Layout value={this.state.value} language={this.state.language} onChangeLanguage={this.languageChange} >                                                   
                            <Route exact path={"/"} component={() => <Home language={this.state.language} />} />
                            <Route path="/about" component={About} /> 
                            <Route path="/services" component={Services} /> 
                            <Route path="/contact" component={Contact} />    
                            <Route path="/devops" component={Devops} />                        
                            <Route path="/jobs" component={Jobs} /> 
                            <Route path="/scrum" component={Scrum} />
                            <Route path="/blog" component={() => <Blog language={this.state.language} />} /> 
                            <Route path="/post" component={() => <PagePost language={this.state.language} />} > 
                                <Route exact path="/post/:url" render={props => <PostSingle {...props} language={this.state.language}/>} />         
                                <Route path="/404" component={NotFound} />
                            </Route>                
                        </Layout>             
                            {/* <Route component={NotFound} /> */}

                                    <Route path='*' exact={true} component={NotFound} /> 
                    </Switch>  
                </Router>
        </TranslatorProvider>
       )
   }

   render(){


    
	   return(
           this.getLayout(this.state.language)
        );
    };    
}; 


export default App;