import React, {Component} from 'react';
import Slider from 'react-slick';
import SeoTitle from '../Title/SeoTitle';
import TestimonialItems from '../Home/TestimonialItems';
import { translate } from "react-translate";
import "./testimonial.css"

const TestimonialEn = require('../../assets/translations/en.json');
const TestimonialEs = require('../../assets/translations/es.json');
let testimonial = null;

class Testimonial extends Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
    this.slider.slickNext();
    }
    previous() {
    this.slider.slickPrev();
    }
   render(){
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 200000,
        slidesToShow: 2,
        slidesToScroll: 1,
        responsive: [
            {
              breakpoint: 700,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
          ]
    };

    let {¬†t , language} = this.props;

    if (language === 'en') {
        testimonial = TestimonialEn
      } else {
        testimonial = TestimonialEs
      }

    
    return(
            <section className="erp_testimonial_area sec_pad " id="testimonial">
                <div className="container ">
                    <SeoTitle  Title={t(this.props.testim_title)} Tclass="text-center" Tclass2="t_color" />
                    <div className="row">
                        <div className="slider_nav">
                            <i className="ti-angle-left prev" onClick={this.previous}></i>
                            <i className="ti-angle-right next" onClick={this.next}></i>
                        </div>
                        <Slider ref={c => (this.slider = c)} className="app_testimonial_info" {...settings}>
                        { testimonial.testimonios.testimoniosItems.map((item, i) => {
                            return (
                                <div className="item" key={i}> 
                                    <TestimonialItems 
                                        NameT = {item.NameT}
                                        Position = {item.Position} 
                                        DescripT ={ item.DescripT} 
                                    />   
                                                                             
                                </div>
                                )
                            })
              
                        }
                        </Slider>
                    </div>
                </div>
            </section>
        )
   }
}

export default translate('testimonios')(Testimonial);

