import React, {Component} from 'react';
import { translate } from "react-translate";
import "./banners.css"

class BannerBottom extends Component {
    render(){

        let {t} = this.props;

        return(
                <div className="servicesInt_content text-center">
                    <div className="">
                        <p className="f_p p_color f_500 f_size_30" >{t(this.props.ServiceBannerDescrip)}</p>
                        <a href="/contact"><h3 className="f_p t_color f_600 f_size_60">{t(this.props.ServiceBannerTitle)}</h3></a>
                    </div>                        
                </div>
        )
    }
}

export default translate('ServiceBannerEnd')(BannerBottom);