import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal';

class Sectitle  extends Component {
    render(){
        var {Title, tClass,sClass} = this.props;
        return(
            <div className={`${sClass} align-items-center`}>
                <h2 className={`f_p  colortxt pb-2 l_height50 f_600 ${tClass}`}>{Title}</h2>
            </div>
        )
    }
}
export default Sectitle;
