import React from 'react';
import ProcessBanner from '../components/Banner/ProcessBanner';
import ScrumTeam from '../components/Process/ScrumTeam';
import ScrumEvents from '../components/Process/ScrumEvents';
import ScrumArtifacts from '../components/Process/ScrumArtifacts';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';
import ReactGA from 'react-ga';
import "../components/Process/process.css";
import Fondo from '../components/Fondo';


    // Configuraci√≥n de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/scrum");

const Scrum = () => {
    return(
        <div className="body_wrapper scrum">
            <MetaDecorator 
                meta_title="metaTitle_Scrum"
                meta_description="metaDescription_Scrum"
                url="scrum"
            />
            <ProcessBanner 
                title_banner="title_banner_Scrum" 
                descri_banner="descri_banner_Scrum"
                classBanner="min-vh-100 text-left bg_grey"
                paddingClass = "pr_0"
                imgDisplay = "d-block"
                img_process="img-scrum-int.png"
            />
            <Fondo ClassFondoImg3="d-none"/>
            <ScrumEvents
                Title="Title"
                Descrip="Descrip"
                event1="event1"
                event1Paragraph1="event1Paragraph1"
                event1Paragraph2="event1Paragraph2"
                event2="event2"
                event2Paragraph1="event2Paragraph1"
                event2Paragraph2="event2Paragraph2"
                event3="event3"
                event3Paragraph1="event3Paragraph1"
                event3Paragraph2="event3Paragraph2"
                event4="event4"
                event4Paragraph1="event4Paragraph1"
                event4Paragraph2="event4Paragraph2"
            />
            <ScrumTeam
                Title="Title"
                Descrip="Descrip"
                Team1="Team1"
                Team1Descrip="Team1Descrip"
                Team2="Team2"
                Team2Descrip="Team2Descrip"
                Team3="Team3"
                Team3Descrip="Team3Descrip"
            />
            <ScrumArtifacts
                Title="Title"
                Descrip="Descrip"
                Artf1="Artf1"
                Artf1Descrip="Artf1Descrip"
                Artf2="Artf2"
                Artf2Descrip="Artf2Descrip"
                Artf3="Artf3"
                Artf3Descrip="Artf3Descrip"
            />
        </div>
    )
}
export default Scrum;