import React, {Component} from 'react';
import { Redirect } from "react-router-dom"
import Markdown from "react-markdown"
import MetaDatosPost from '../MetaDatos/MetaDatosPost';
import ReactGA from 'react-ga';
import postlist from "../../posts.json"
import postlistES from "../../postsES.json"

class PostSingle extends Component {
    
    render(){

        let {¬†props, language} = this.props;

        let validId = this.props.match.params.url;
        const fetchedPost = {}
        let postExists = false

        if (language === "es") {

            postlistES.forEach((post, i) => {
                if (validId === post.url) {
                    fetchedPost.title = post.title ? post.title : "No title given"
                    fetchedPost.metaTitle = post.metaTitle ? post.metaTitle : "No metaTitle given"
                    fetchedPost.metaDescription = post.metaDescription ? post.metaDescription : "No metaDescription given"
                    fetchedPost.date = post.date ? post.date : "No date given"
                    fetchedPost.read = post.read ? post.read : "No read given"
                    fetchedPost.url = post.url ? post.url : "No url given"
                    fetchedPost.imglink = post.imglink ? post.imglink : "No imglink given"
                    fetchedPost.category = post.category ? post.category : "No category given"
                    fetchedPost.content = post.content ? post.content : "No content given"
                    postExists = true
                }   
            })
        } else {

            postlist.forEach((post, i) => {
                if (validId === post.url) {
                    fetchedPost.title = post.title ? post.title : "No title given"
                    fetchedPost.metaTitle = post.metaTitle ? post.metaTitle : "No metaTitle given"
                    fetchedPost.metaDescription = post.metaDescription ? post.metaDescription : "No metaDescription given"
                    fetchedPost.date = post.date ? post.date : "No date given"
                    fetchedPost.read = post.read ? post.read : "No read given"
                    fetchedPost.url = post.url ? post.url : "No url given"
                    fetchedPost.imglink = post.imglink ? post.imglink : "No imglink given"
                    fetchedPost.category = post.category ? post.category : "No category given"
                    fetchedPost.content = post.content ? post.content : "No content given"
                    postExists = true
                }   
            })
            
        }




    
        if (postExists === false) {
            return <Redirect to="/blog" />
        }

        // Configuraci√≥n de Google Analytics
        const gaTrackingId = "UA-194698672-1"; 
        ReactGA.initialize (gaTrackingId); 
        ReactGA.pageview ("post/" + fetchedPost.url );

        return (
            <section className="post_area">
                <MetaDatosPost 
                    MetaTitle={fetchedPost.metaTitle}
                    MetaDescription={fetchedPost.metaDescription}
                    url= {"post/" + fetchedPost.url}
                />
                <div className="post-img">
                    <img className="img-fluid" src={require(`../../img/posts-Img/${fetchedPost.imglink}`).default} alt=""/>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-2 col-sm-12"></div>
                        <div className="col-lg-8 blog_sidebar_left">
                            <div className="blog_single mb_50">
                            
                                <div className="blog_content">
                                    <h1 className="f_p f_size_50 f_600 t_color mt-3">{fetchedPost.title}</h1>
                                    <p>{fetchedPost.date} - Read {fetchedPost.read} </p>
                                    
                                    <div className="mt-4">  
                                        <Markdown children={fetchedPost.content}  skipHtml={false} />
                                    </div> 
                                    <div className="post_tag d-flex">
                                        <p>{fetchedPost.category}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-2">
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
export default PostSingle;