import React, {Component} from 'react';

class BenefitsDevOpsItem extends Component {
    render(){
        let { sTitle, Descrip, image} = this.props;
        return(
            <div className="col-lg-4 col-sm-6  fadeInUp" data--delay="0.4s">
                <div className="software_featured_item text-left mb_20">
                    <img src={require ("../../img/process/" + image).default} alt=""/>
                    <h3 className="f_600">{sTitle}</h3>
                    <p className=" f_size_20 l_height mb_40">{Descrip}</p>
                </div>
            </div>
        )
    }
}
export default BenefitsDevOpsItem