import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal/';
import { translate } from "react-translate";
import "./banners.css"


class SecBanner extends Component{
    render(){


            let {¬†t , idBanner, classBanner, imgDisplay, paddingClass} = this.props;

        return(
            <section className={`banner_area ${classBanner} sec_pad mt-5`} id={idBanner}>
                <div className="container">
                
                        <div className="row">
                            <div className="col-md-12  seo_banner_content ">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h1 className="mb-5">{t(this.props.title_banner)}</h1>
                                
                                </Reveal>
                                <Reveal effect="fadeInUp" duration={1000}>
                                    <p className={`fadeInUp mb-5 ${paddingClass} `} data--delay="0.5s">
                                        {t(this.props.descri_banner)}</p>
                                </Reveal>
                                
                            </div>
                        </div>
                        <section id="section10" class="demo text-center mt-5">
                                    <div><span></span></div>
                        </section>
                        <p className="f_p p_color text-center f_600">Scroll</p>

                </div>
                <img className={`svg_intro_bottom ${imgDisplay}`} src={require ('../../img/banner/shape.png').default}  alt=""/>
            </section>
        )
    }    
}


export default translate('sec_banner')(SecBanner);
