import React, {Component} from 'react';
import { translate } from "react-translate";
import "./process.css"

class DevOpsItem extends Component {
    render(){

        let {¬†t } = this.props;

    return(
        <section className="devops_part_area pb-5 sec_pad">
            <div className="container">
                <div className="d-flex">
                    <div className="devops_part_item">
                        <img src={require ('../../img/process/DevIcon.png').default} alt=""/>
                        <h4>Dev</h4>
                        <p className="p_color f_500">{t(this.props.Dev_txt)}</p>
                    </div>
                    <div className="devops_part_item">
                        <img src={require ('../../img/process/OpsIcon.png').default} alt=""/>
                        <h4>Ops</h4>
                        <p className="p_color f_500">{t(this.props.Ops_txt)}</p>
                    </div>
                </div>
            </div>
        </section>
        )
    }
}

export default translate('DevOpsItem')(DevOpsItem);


