import React, {Component} from 'react';
import { translate } from "react-translate";
import Reveal from 'react-reveal/Reveal/';
import "./banners.css"

class JobsBanner extends Component{

    render(){

    let {t} = this.props;

    return (
        <section className="jobs_banner_area">
            <img className="devops_banner_img" src={require ("../../img/banner/bannerJob.png").default} alt=""/>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12 align-items-center">
                        <Reveal effect="fadeInLeft" duration={1200}>
                            <div className="jobs_banner_content text-center">
                                <h2 className="t_color f_600 f_size_70 fadeInUp" data--delay="0.3s">{t(this.props.title_banner_Jobs)}</h2>
                            </div>
                        </Reveal>
                    </div>

                </div>
            </div>
        </section>
        )
    }    
}
export default translate('sec_banner')(JobsBanner);
