import React, {Component} from 'react';

class OurClients extends Component{
    render(){

        return(
        <React.Fragment>
            <section className="our_clients_area sec_area bg-white " id="clients">
                <div className="container">
                    <div className="our_clients_content">
                        <div className="row">
                        <div className="col-lg-12">
                            <div className="row our_clients_logo">
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-target.png').default} alt=""/></div>
                                </div>                               
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-falabella.png').default} alt=""/></div>    
                                </div>
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-miraclon.png').default} alt=""/></div>
                                </div>
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-bolivar.png').default} alt=""/></div>
                                </div>
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-phonecheck.png').default} alt=""/></div>
                                </div>
                                <div className="col-lg-4 col-6 our_clients_item">
                                    <div className="intregration_icon"><img src={require('../../img/clients/logo-sequoia.png').default} alt=""/></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
        )
    }
}
export default OurClients;